import React from 'react';
import './HorizontalIcon.css';
// eslint-disable-next-line
export default class HorizontalIcon extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            listItems: []
        }
        this.handleClick = this.handleClick.bind(this);
    }

    componentDidMount() {
        var listItems = this.props.styles.map((item, index) =>
            this.forEachLi(item.style, index)
        )
        this.setState({ listItems: listItems });
    }
    
    componentWillReceiveProps(newProps) {
        var listItems = newProps.styles.map((item, index) =>
            this.forEachLi(item.style, index)
        )
        this.setState({ listItems: listItems });
    }


    forEachLi(style, index,style2) {
        return (
            <div className="icon-each2" style={style2} key={index} data-index={index} onClick={this.handleClick} >
                <div className="icon-icon2" style={style} data-index={index} onClick={this.handleClick}  >
                </div>
            </div>
        )
    }

    handleClick(event) {
        var indexClick = event.target.getAttribute("data-index");
        var listItems2 = []
        var listItem
        for (let index = 0; index < this.props.styles.length; index++) {
            var element = this.props.styles[index];
            if (indexClick == index) {
                element.bln = !element.bln
                var style2={border:"4px solid rgba(255, 101, 39, 1)","boxSizing":"border-box"};
                listItem = this.forEachLi(element.style, index,style2)

                // if (element.function !== undefined) {
                //     element.function(element.bln)
                // }

            }
            else {
                if (element.bln === false) {
                    listItem = this.forEachLi(element.style, index)
                }
                else {
                    element.bln = false
                    listItem = this.forEachLi(element.style, index)
                    // if (element.function !== undefined) {
                    //     element.function(false)
                    // }
                }
            }
            listItems2.push(listItem)
        }
        this.setState({
            listItems: listItems2
        })
    }


    render() {
        
        return (
            <div className="icons-container2" style={this.props.stylePosition}>
                    {this.state.listItems}
            </div>
        )
    }
}