import React from 'react';
import './HorizontalLabel.css';

// eslint-disable-next-line

export default class HorizontalLabel extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            listItems: [],
            blndownorup:false,
            styledown: {
                backgroundImage: "url(./images/down.svg)",
            }, 
            styleup: {
                backgroundImage: "url(./images/up.svg)",
            }, 
            styledownmove: {
                transform: "translateY(0px)"
            }, 
            styledownup: {
                transform: "translateY(70px)"
            }, 
        



        };
        // 这边绑定是必要的，这样 `this` 才能在回调函数中使用
        this.handleClick = this.handleClick.bind(this);
        this.handleClick2 = this.handleClick2.bind(this);
        this.forEachLi = this.forEachLi.bind(this)
        this.cancelFunction = this.cancelFunction.bind(this)
    }

    componentDidMount() {
        var listItems = this.props.imageFunctions.map((item, index) =>
            this.forEachLi(item.bln === false ? item.image : image.imageclick, index)
        )
        this.setState({ listItems: listItems });
    }

    componentWillReceiveProps(newProps) {
        
        var listItems=[]
        for (let index = 0; index < newProps.imageFunctions.length; index++) {
            const element = newProps.imageFunctions[index];
            listItems.push(this.forEachLi(element.bln === false?element.image:element.imageclick, index))
        }
        this.setState({ listItems: listItems });
    }



    componentWillUnmount() {
        this.cancelFunction();
    }

    
    cancelFunction() {
        var listItems2 = []
        var listItem
        for (let index = 0; index < this.props.imageFunctions.length; index++) {
            var element = this.props.imageFunctions[index];
            if (element.bln === false) {
                listItem = this.forEachLi(element.image, index)
            }
            else {
                element.bln = false
                listItem = this.forEachLi(element.image, index)
                if (element.function !== undefined) {
                    element.function(null,element)
                }
            }
            listItems2.push(listItem)
        }
        this.setState({
            listItems: listItems2
        })

    }




    forEachLi(strImageSrc, index) {
        return (
            <div className="icon-each3" key={index}  >
                <img src={strImageSrc} data-index={index} onClick={this.handleClick} className="icon-each-img3" />
            </div>
        )
    }


    handleClick(event) {
        var indexClick = event.target.getAttribute("data-index");
        var listItems2 = []
        var listItem
        for (let index = 0; index < this.props.imageFunctions.length; index++) {
            var element = this.props.imageFunctions[index];
            if (indexClick == index) {
                element.bln = !element.bln
                if (element.function !== undefined) {
                    element.function(event,element)
                }
                listItem = this.forEachLi(element.bln === false ? element.image : element.imageclick, index)
            }
            if(indexClick != index)  {
                if (element.bln === false) {
                    listItem = this.forEachLi(element.image, index)
                }
                else {
                    element.bln = false
                    if (element.function !== undefined) {
                        element.function(event,element)
                    }
                    listItem = this.forEachLi(element.image, index)
                }
            }
            listItems2.push(listItem)
        }
        this.setState({
            listItems: listItems2
        })
    }


    handleClick2(event){
        this.cancelFunction();
        this.setState({ 
            blndownorup: !this.state.blndownorup
        })
    }



    render() {
        return (
            
            <div className='icon-containerparent3' style={this.state.blndownorup?this.state.styledownup:this.state.styledownmove}>
                <div className="LowerMenuOpenClose3" style={this.state.blndownorup?this.state.styleup:this.state.styledown}  onClick={this.handleClick2}></div>
                <div className="icon-container3" >
                    {this.state.listItems}
                </div>
            </div>
        )
    }
}