import React from 'react';
import './QRcode.css';
// eslint-disable-next-line
export default class QRcode extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            strHtml: ""
        }
        this.handleClick = this.handleClick.bind(this);
    }

    componentDidMount() {

        var html = <div className="divbackground">
            <div className="divQRcodeContainer2">
                <img src="./images/close.svg" className="imgclose" onClick={this.handleClick} />
                <img src="./images/QRcodescan.png" className="imgQRcode" />
                <div className="divcompanyandcontact">
                    <div className="company"><img src="./images/company.svg" />官方网站：www.guanfang.com</div>
                    <div className="contact"><img src="./images/phone.svg" />联系方式：188 1234 5678</div>
                </div>
            </div>
        </div>

        this.setState({ strHtml: html })

    }



    handleClick(event) {
        // this.setState({ strHtml: "" })
        this.props.target.click()
    }

    // componentWillUnmount() {
    //     console.log(123)
    // }





    render() {
        return (
            <div id="Contactid">
                {this.state.strHtml}
            </div>
            
        )
    }
}