import React from 'react';
import './Videoplay.css';
import { Player } from 'video-react';
import 'video-react/dist/video-react.css'; // import css

// eslint-disable-next-line
export default class Videoplay extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            blnshow: true,
        };
        this.CloseVideo = this.CloseVideo.bind(this);
    }



    CloseVideo() {
        this.setState({ blnshow: false });
    }


    render() {
        return (
            this.state.blnshow ?
                <div className="mask-layer2" id="maskLayer">
                    <div className="video-box2" id="videoBox">

                    <Player
                        playsInline
                        src="./video/病毒篇15s.mp4"
                        autoPlay
                    />

                        {/* <video src="./video/病毒篇15s.mp4" autoplay="autoplay" controls="controls" playsinline="false" x5-video-player-type="h5" x5-video-orientation="h5" x5-video-player-fullscreen="true" preload="auto" controlslist="nodownload" disablePictureInPicture></video> */}
                        <div className="close-box2" id="closeBox">
                            <img src="./images/close2.svg" onClick={this.CloseVideo}></img>
                        </div>
                    </div>
                </div>
                : null

        )
    }
}