import React from 'react';
import './Robot.css';

// eslint-disable-next-line
export default class Robot extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            strHtml: ""
        }
        this.handleClick = this.handleClick.bind(this);
    }

    componentDidMount() {

        var html =
                <img src="./images/robot.png" className="robot-question2" />

        this.setState({ strHtml: html })

    }



    handleClick(event) {
        // this.setState({ strHtml: "" })
        this.props.target.click()
    }

    // componentWillUnmount() {
    //     console.log(123)
    // }





    render() {
        return (
            <div>
                {this.state.strHtml}
            </div>

        )
    }
}