import React from 'react';
import './Videoplay.css';
import { Player, BigPlayButton } from 'video-react';
import 'video-react/dist/video-react.css'; // import css

// eslint-disable-next-line
export default class Videoplay extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            blnshow: true,

        };
        this.CloseVideo = this.CloseVideo.bind(this);


    }

    CloseVideo() {
        this.setState({ blnshow: false });
    }


    render() {
        return (
            this.state.blnshow ?
                <div className="mask-layer" id="maskLayer">
                    <div className="video-box" id="videoBox">

                    <Player
                        src="./video/病毒篇15s.mp4"
                        autoPlay
                        aspectRatio="4:3"
                        >
                    <BigPlayButton position="center" />
                    </Player>
                    </div>
                    <div className="close-box">
                        <img src="./images/close2.svg" onClick={this.CloseVideo} />
                    </div>
                </div>
                : null

        )
    }
}