/**
 * React+Three.js 三维地球
 */
import * as THREE from 'three/build/three.module.js';

// import Orbitcontrols from 'three-orbitcontrols';
import {
    OrbitControls
} from 'three/examples/jsm/controls/OrbitControls.js';
import { TWEEN } from 'three/examples/jsm/libs/tween.module.min.js';

class ThreeMap {

    initThree(id) {

        let camera, controls;
        let renderer;
        let scene;

        init();
        animate();
        initsprite();
        window.scene = scene
        window.THREE = THREE

        function init() {

            const container = document.getElementById(id);

            renderer = new THREE.WebGLRenderer({
                antialias: true
            });
            renderer.antialias = true; // 抗锯齿
            renderer.autoClear = true; // 自动清除
            renderer.setPixelRatio(window.devicePixelRatio); //像素比 一般都是这个
            //renderer.outputEncoding = sRGBEncoding; //why
            //renderer.textureEncoding = sRGBEncoding; //why
            //renderer.setSize(width, height);
            renderer.setSize(window.innerWidth, window.innerHeight);
            //document.getElementById('canvas-frame').appendChild(renderer.domElement); //canvas-frame元素中插入canvas对象
            renderer.setClearColor(0xFFFFFF,
                1.0
            ); //renderer.setClearColor(0xEEEEEE, 0.0);或者renderer.setClearAlpha(0.0);它的意思是每次绘制清除缓冲区后的颜色通道设置,简单就是设置每次绘制的背景颜色
            //renderer.toneMapping = THREE.ACESFilmicToneMapping; //色彩鲜明 色彩不鲜明
            renderer.toneMappingExposure = 1;
            // renderer.setClearAlpha(0.8); //setClearAlpha值，为背景透明度
            // renderer.shadowMap.enabled = true; //3-4 point
            // renderer.shadowMap.type = THREE.PCFSoftShadowMap; //阴影都是马赛克
            // renderer.gammaInput = true;
            // renderer.gammaOutput = true; //更鲜艳



            container.appendChild(renderer.domElement);

            scene = new THREE.Scene();

            camera = new THREE.PerspectiveCamera(90, window.innerWidth / window.innerHeight, 0.1, 2000);
            camera.position.z = 0.01;

            controls = new OrbitControls(camera, renderer.domElement);
            controls.enableZoom = true;
            controls.enablePan = true;
            controls.enableDamping = true;
            controls.rotateSpeed = -0.25;

            var light = new THREE.AmbientLight(0x404040);
            light.intensity = 10;
            scene.add(light);
            // scene.background = new THREE.Color(0x000000); //迷雾 这样设置就会导致你设置渲染alpha的透明度没作用,



            const textures = [];
            textures.push(new THREE.TextureLoader().load("./AboutCar/panorama/Lightgrey/r.jpg"))
            textures.push(new THREE.TextureLoader().load("./AboutCar/panorama/Lightgrey/l.jpg"))
            textures.push(new THREE.TextureLoader().load("./AboutCar/panorama/Lightgrey/u.jpg"))
            textures.push(new THREE.TextureLoader().load("./AboutCar/panorama/Lightgrey/d.jpg"))
            textures.push(new THREE.TextureLoader().load("./AboutCar/panorama/Lightgrey/f.jpg"))
            textures.push(new THREE.TextureLoader().load("./AboutCar/panorama/Lightgrey/b.jpg"))



            const materials = [];

            for (let i = 0; i < 6; i++) {

                materials.push(new THREE.MeshBasicMaterial({
                    map: textures[i],
                    transparent: true,
                }));

            }

            const skyBox = new THREE.Mesh(new THREE.BoxBufferGeometry(10, 10, 10), materials);
            skyBox.geometry.scale(1, 1, -1);
            scene.add(skyBox);
            scene.fog = new THREE.Fog(0xcce0ff, 500, 10000);
            var geo= new THREE.BoxGeometry()
            var material = new THREE.MeshBasicMaterial({color: 0x0000ff})
            var mesh3 = new THREE.Mesh(geo, material);
            scene.add(mesh3)

            window.addEventListener('resize', onWindowResize, false);

        }


        function onWindowResize() {

            camera.aspect = window.innerWidth / window.innerHeight;
            camera.updateProjectionMatrix();

            renderer.setSize(window.innerWidth, window.innerHeight);

        }

        function animate() {

            requestAnimationFrame(animate);
            
            controls.update(); // required when damping is enabled
            TWEEN.update()
            renderer.render(scene, camera);

        }

        function initsprite() {
            var biaobiaoArr = []
            let objects = [{
                position: {
                    x: -1.52,
                    y: -0.58,
                    z: -3.00
                },
                name:"videoplay1",
                img: './images/playvideo.png',
            },{
                position: {
                    x: -2.14,
                    y: -1.24,   
                    z: -3.00
                },
                name:"videoplay1",
                img: './images/showpic.png',
            },{
                position: {
                    x: 0,
                    y: -0.38,   
                    z: -3.00
                },
                name:"videoplay1",
                img: './images/playvideo.png',
            },{
                position: {
                    x: -0.70,
                    y: -2.82,   
                    z: -3.0
                },
                name:"videoplay1",
                img: './images/playvideo.png',
            },{
                position: {
                    x: 2.0,
                    y: -4.5,   
                    z: -3.0
                },
                name:"videoplay1",
                img: './images/playvideo.png',
            },{
                position: {
                    x: 1.62,
                    y: -0.78,   
                    z: -2.66
                },
                name:"videoplay1",
                img: './images/playvideo.png',
            },
            {
                position: {
                    x: 2.42,
                    y: 0.16,   
                    z: -2.22
                },
                name:"videoplay1",
                img: './images/playvideo.png',
            }
            ,
            {
                position: {
                    x: 2.22,
                    y: -0.64,   
                    z: -1.52
                },
                name:"videoplay1",
                img: './images/showpic.png',
            }
        
        ]
            var initTween = function (obj) { //tween 动画
                //new TWEEN.Tween(meshCylinder22.position)
                //        .to( { x: -100 }, 2000 ).repeat( Infinity ).start();
                let tweenSphere = new TWEEN.Tween(obj.scale);
                tweenSphere.to({
                    x: 0.13,
                    y: 0.13,
                    z: 0.13
                }, 2000);
                tweenSphere.start();
                tweenSphere.onUpdate(function () {
                    //console.log("x: "+this.x);
                });

                let tweenSphereBack = new TWEEN.Tween(obj.scale);
                tweenSphereBack.to({
                    x: 0.1,
                    y: 0.1,
                    z: 0.1
                }, 2000);
                tweenSphereBack.onUpdate(function () {})
                tweenSphere.chain(tweenSphereBack);
                tweenSphereBack.chain(tweenSphere);
            }
            for (let i = 0; i < objects.length; i++) {
                //图标
                var spriteMap = new THREE.TextureLoader().load(objects[i].img);
                spriteMap.needsUpdate = true;
                var spriteMaterial = new THREE.SpriteMaterial({
                    map: spriteMap,
                    color: 0xffffff,
                    transparent: true,
                    side: THREE.DoubleSide,
                    sizeAttenuation: false,
                }) //// 粒子的大小是否和其与摄像机的距离有关，默认值 truesizeAttenuation: true,
                var sprite = new THREE.Sprite(spriteMaterial);
                sprite.position.set(objects[i].position.x,objects[i].position.y,objects[i].position.z)
                sprite.scale.set(0.1, 0.1, 0.1)
                sprite.renderOrder=1
                // sprite.lookat(0,0,0)
                biaobiaoArr.push(sprite)
                initTween(sprite)
                scene.add(sprite)
            }





            var raycaster = new THREE.Raycaster();
            var mouse = new THREE.Vector2();

            function onTouchMove(event) {
                var x, y;
                x = event.clientX;
                y = event.clientY;
                mouse.x = (x / window.innerWidth) * 2 - 1;
                mouse.y = -(y / window.innerHeight) * 2 + 1;
                raycaster.setFromCamera(mouse, camera);
                var intersects = raycaster.intersectObjects(biaobiaoArr);
                if (intersects.length > 0) {
                    document.body.style.cursor = 'pointer';
                } else {
                    document.body.style.cursor = '';
                }
            }
            window.addEventListener('mousemove', onTouchMove);



        }


    }

}

export default ThreeMap;